*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
}
html{
    scroll-behavior: smooth;
}

body{
    background-color: #000;
    color: #fff;
    font-family: Satoshi,sans-serif;
}

header{
    width: 100%;
}
ul{
    list-style-type: none;
}

nav{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}
.logo, nav ul{
    display: flex;
    align-items: center;
    gap: 10px;
}

.logo>:first-child{
    width: 20px;
    height: 20px;
    background-color: #fff;
    animation: rotate 5s linear infinite;
}
.logo h3{
    font-size: 2rem;
}

nav ul li{
    padding: 10px;
    cursor: pointer;
    font-size: 1.2rem;
}
nav ul span{
    visibility: hidden;
    opacity: 0.6;
}
nav ul>:first-child span{
    visibility: visible;
}

nav ul li:hover span{
    visibility: visible;
}

main{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    height: 100%;
    min-height: 70vh;
    min-height: 70svh;
    width: 100%;
    max-width: 1300px;
    margin: 50px auto;
    text-align: center;
    animation: entry 1s ease;
}
.name h3{
    font-size: 3rem;
    padding: 20px;
}
.name p{
    font-size: 2rem;
    color: #dbd4d4;
}

.name span{
    font-size: 2.5rem;
    padding: 20px;
    color: #dbd4d4;
}
.underline{
    width: 80%;
    margin:20px auto;
    height: 2px;
    background-color: #fff;
    
}
.circle{

    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
   
}
.circle1,.circle2,.circle3,.circle4,.circle5{
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: rotate 30s infinite linear;
}
.circle1:hover , .circle1:hover .circle2 ,.circle1:hover .circle3 ,.circle1:hover .circle4 ,.circle1:hover .circle5 {
    animation-play-state: paused;
}

.circle1{
    width: 300px;
    height: 300px;
    background-color: #fff;
}
.circle2{
    width: 250px;
    height: 250px;
    border: 1px solid white;
    background-color: #000;
}
.circle3{
    width: 200px;
    height: 200px;
    background-color: #fff;
    border: 1px solid #000;
}
.circle4{
    width: 150px;
    height: 150px;
    border: 1px solid white;
    background-color: #000;
}
.circle5{
    width: 100px;
    height: 100px;
    background-color: #fff;
    border: 1px solid #000;
    color: #000;
    font-size: 2rem;
}
.circle5 h4 span{
    animation: blink 1s linear infinite;
}

.about{
    width: 80%;
    max-width: 1300px;
    margin: 100px auto;
    display: grid;
    gap: 20px;
    grid-template-columns: 2fr 1fr;
    text-align: center;
}
.about>:first-child{
    grid-column: 1/-1;
    font-size: 3rem;
    margin: 0 auto;
    padding: 20px;
    text-transform: uppercase;
    border-bottom: 1px solid #fff;
    width: 100%;
    margin-bottom: 50px;
}

.about>:nth-child(2) h4, .about>:nth-child(3) h4{
    font-size: 2.5rem;
    padding: 30px;
}
.about>:nth-child(2){
    padding-bottom: 50px;
    border-right: 1px solid #fff;
}
.about>:nth-child(2) p{
    width: 70%;
    margin: auto;
    color: #bbb7b7;
    font-weight: bold;
    line-height: 1.5rem;
}
.about>:nth-child(2) p span{
    font-size: 2rem;
    color: #000;
    text-shadow: 0 0 5px #fff;
}

.skills{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

}
.skill svg{
    width: 80px;
    height: 80px;
    cursor: pointer;
    transition: all 0.3s ease;
    
}
.skills .skill{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    color: #bbb7b7;
}
.skills>:first-child>:first-child:hover{
    color: #f06529;
}
.skills>:first-child>:nth-child(2):hover{
    color: #2965f1;
}
.skills>:first-child>:nth-child(3):hover{
    color: yellow;
}
.skills>:first-child>:nth-child(4):hover{
    color:#61dbfb;
}
.skills>:first-child>:nth-child(5):hover{
    color: #f34f29;
}
.skills>:first-child>:nth-child(6):hover{
    color: rgb(26, 97, 26);
}

.skills>:nth-child(2),.skills>:nth-child(3){
    font-size: 1.2rem;
    color: #bbb7b7;
}

.projects{
    width: 80%;
    max-width: 1300px;
    margin: 100px auto;
    text-align: center;
}
.mainProject{
    width: 100%;
}
.mainProject .mp{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.mp p{
    line-height: 1.5rem;
}
.mainProject img{
    transition: all 0.3s ease;
    width: 100%;
    transform: scale(0.8);
    box-shadow: 0px 0px 5px #fff;
    transition: all 0.5s ease;
}
.mp div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;

}
.mp h3{
    font-size: 2.5rem;
    border-bottom: 1px solid #fff;
    margin-bottom: 10px;
    padding-bottom: 10px;
}
.mp button{
    padding: 10px 20px;
    border-radius: 30px;
    margin-top: 10px;
    border: 1px solid #fff;
    background-color: transparent;
    transition: all 0.3s ease;
    /* color: #fff; */
}
.mp button:hover{
    cursor: pointer;
    background-color: #fff;
    color: #000;
    box-shadow: 0px 0px 5px 1px #fff;
}
.mp button:hover a{
    color: #000;
}
.mainProject img:hover{
    transform: scale(1);
    cursor: pointer;
}

.projects>:first-child{
    position: sticky;
    top: 0;
    padding: 20px;
    font-size: 3rem;
    text-transform: uppercase;
    border-bottom: 1px solid #fff;
    z-index: -1;
    margin-bottom: 50px;
}
a{
    text-decoration: none;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 30px;
}
.cards .card{
    width: 300px;
    height: 400px;
    margin: 50px 10px;
    border: 1px solid white;
    transition: transform 0.3s ease;

}
.card:hover{
    cursor: pointer;
    transform: translateY(-10px);
}
.card img{
    width: 100%;
    height: 100%;
}
.contact{
    width: 80%;
    max-width: 1300px;
    margin: auto;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
}
.text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 10px;
}

.text h3{
    font-size: 3rem;
    padding: 20px;
    border-bottom: 1px solid #fff;
    text-transform: uppercase;
}
.text p{
    font-size: 1.2rem;
    opacity: 0.8;
}
a{
    text-decoration: none;
    color: #fff;
}
.text button{
    padding: 10px 20px;
    border-radius: 30px;
    border: 1px solid #fff;
    color: #fff;
    background-color: transparent;
    transition: all 0.3s ease;
    /* color: #fff; */
}
.text button:hover{
    

    cursor: pointer;
    background-color: #fff;
    color: #000;
    box-shadow: 0px 0px 5px 1px #fff;

}
.form button{
    padding: 10px 20px;
    border-radius: 36px;
    background-color: #fff;
    color: #000;
    border: none;

}
.form button:hover{
    opacity: 0.7;
    cursor: pointer;
}
.form>:first-child{
    display: grid;
    grid-template-columns: 1fr;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 10px;
    width: 80%;
    margin-top: 30px;
}

.form input{
    padding: 10px;
    width: 100%;
    outline: none;
    
}
.form textarea{
    width: 100%;
    height: 100px;
    padding: 10px;
    outline: none;
    resize: none;
    text-transform: capitalize;
}
.form button{
    border-radius: 0;
}
input:hover,input:focus,textarea:hover,textarea:focus{
    outline: 1px solid rgb(86, 86, 248);
}

footer{
    width: 100%;
    border-top: 1px solid #fff;
    margin-top: 50px;
}
footer section{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    padding: 50px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 20px;
}
footer section>:nth-child(2){
    align-self: flex-end;
}

footer section article h3{
    font-size: 2rem;
    padding: 20px 0;
}
footer section article p{
    padding: 10px;
    opacity: 0.8;
}
footer>:first-child>:first-child>:nth-child(2),footer>:first-child>:first-child>:nth-child(2) a{
    display: flex;
    align-items: center;
    gap: 20px;
}
footer>:first-child>:first-child>:nth-child(2) svg{
    font-size: 1.5rem;
}
footer>:first-child>:first-child>:last-child{
    display: none;
}
footer>:first-child>:last-child p:hover{
    cursor: pointer;
    opacity: 1;
}
.thankyou{
    margin:100px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    padding: 30px;
    
}
.back{
    margin-top: 50px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

}
.back h3{
    font-size: 2rem;
}
.back button{
    padding: 10px;
    border: none;
    background-color: #fff;
    color: #000;
    border-radius: 36px;
    font-size: 1.5rem;

}
.back button:hover{
    cursor: pointer;
    opacity: 0.8;
}

@keyframes blink {
    0%{
        opacity: 1;
    }
    50%{
        
        opacity: 0;
    }
    100%{
        opacity: 1;

    }
    
}

@keyframes rotate {
    0%{
        border-radius: 0;
        transform: rotate(0);
    }
    50%{
        border-radius: 50%;
        transform: rotate(180deg);
    }
    100%{
        border-radius: 0;
        transform: rotate(360deg);
    }
}

@keyframes entry {
    0%{
        opacity: 0.2;
    }
    100%{
        opacity: 1;
    }
}

.entry{
    animation: entry 0.8s ease;
}


@media screen and (max-width:1000px) {
    .circle1{
        width: 250px;
        height: 250px;
        background-color: #fff;
    }
    .circle2{
        width: 200px;
        height: 200px;
    }
    .circle3{
        width: 150px;
        height: 150px;
    }
    .circle4{
        width: 100px;
        height: 100px;
    }
    .circle5{
        width: 50px;
        height: 50px;
        font-size: 1.5rem;
    }
    .circle5 h4{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .text{
        text-align: center;
    }
    .form>:first-child {
        width: 100%;
    }
    .cards{
        padding: 0;
    }
    .cards .card{
        width: 250px;
        height: 350px;
    }
}
@media screen and (max-width:768px) {
    nav{
        flex-wrap: wrap;
        justify-content: center;
        gap: 20px;
    }
    .name h3{
        font-size: 2.3rem;
    }
    .name p{
        font-size: 1.5rem;
    }
    .about>:first-child,.projects>:first-child{
        font-size: 2rem;
    }
    .about>:nth-child(2) h4, .about>:nth-child(3) h4 {
        font-size: 1.5rem;
    }
    .about>:nth-child(2) p {
        width: 100%;
        padding-right: 10px;
        font-size: 1rem;
    }
    .skill svg{
        width: 50px;
        height: 50px;
    }
    .skills>:nth-child(2), .skills>:nth-child(3) {
        font-size: 1rem;
    }
    
    .mp h3{
        font-size: 2rem;
    }
    .mp p{
        display: none;
    }
    .text h3{
        font-size: 2rem;
    }
    .cards .card{
        width: 200px;
        height: 300px;
    }
    .contact{
        grid-template-columns: 1fr;
    }
    .text h3{
        border-bottom: none;
    }
    .text p{
        display: none;
    }
    .text{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        border-bottom: 1px solid #fff;
    }
    .text a{
        padding-right: 20px;
    }
    .form>:first-child {
        margin-top: 0;
    }
    footer>:first-child>:first-child>:last-child{
        display: block;
    }
}
@media screen and (max-width:550px) {
    nav ul{
        display: none;
    }
    main{
        grid-template-columns: 1fr;
    }
    .cards .card{
        width: 150px;
        height: 240px;
    }
    .name{
        order: 2;
    }
    .mainProject .mp{
        grid-template-columns: 1fr;
    }
    .circle1{
        width: 200px;
        height: 200px;
        background-color: #fff;
    }
    .circle2{
        width: 150px;
        height: 150px;
    }
    .circle3{
        width: 100px;
        height: 100px;
    }
    .circle4{
        width: 50px;
        height: 50px;
    }
    .circle5{
        width: 25px;
        height: 25px;
        font-size: 0.8rem;
    }
    .about{
        grid-template-columns: 1fr;
    }
    .about>:nth-child(2) {
        border-right: none;
        border-bottom: 1px solid #575555;
       
    }
}

